import React from 'react';
import styled from 'styled-components';
import DrumPad from './components/DrumPad';
import './style.css';

const AppWrapper = styled.div `

background: rgb(44,13,255);
background: linear-gradient(0deg, rgba(44,13,255,1) 0%, rgba(97,82,255,1) 54%, rgba(117,139,253,1) 100%);
    display: grid;
    grid-template-rows: 180px;
    justify-content: center;
    min-height: 100vh;
    width: 100vw;
    
    h1.heading{
        align-items: center;
        display: grid;
        height: 150px;
        justify-items: center;

        @media(max-width: 768px){
            font-size: 1.7rem;
        }

        @media(max-width: 415px){
            font-size: 1.3rem;          
        }
    }

    .drum-pad-container{
        align-content: space-around;
        background-color: #102542;
        border: 2px solid black;
        border-radius: 8px;
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(3, 120px);
        grid-template-rows: repeat(3, 120px);
        height: 400px;
        justify-items: center;
        padding: 10px;
        

        @media(max-width: 768px){
            grid-template-columns: repeat(3, 100px);
            grid-template-rows: repeat(3, 100px);
            height: 340px;
        }

        @media(max-width: 415px){
            grid-template-columns: repeat(3, 80px);
            grid-template-rows: repeat(3, 80px);
            height: 270px;         
        }
    }

`

const data = [
    {id: 'Low Bass', letter: 'Q', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/05-22dbl%20hd%20kick%20a.wav'},
    {id: 'High Bass', letter: 'W', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/02-22deep%20kic.wav'},
    {id: 'Hi Hat', letter: 'E', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/28-foot%20closed%20hat.wav'},
    {id: 'Snare Drum 1', letter: 'A', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/16-rimshot%20snare.wav'},
    {id: 'Snare Drum 2', letter: 'S', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/13-13%20brass%20picolo.wav'},
    {id: 'High Conga Slap', letter: 'D', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/33high%20conga%20slap.wav'},
    {id: 'Low Conga Slap', letter: 'Z', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/34-low%20conga%20slap.wav'},
    {id: 'Snap', letter: 'X', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/48-finger%20snaps.wav'},
    {id: 'Clap', letter: 'C', src: 'http://www.burnkit2600.com/temp/HR-16/HR-16-WAVs/47-hand%20claps.wav'}
]

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            display: 'Click a button or press a key'
        }
    }

    handleDisplay = display => this.setState({ display })

    render() {
        return(
            <AppWrapper>
                <h1 className='heading'>{this.state.display}</h1>
                <div className='drum-pad-container'>{data.map(({ id, letter, src }) => (
                    <DrumPad 
                        handleDisplay={this.handleDisplay}
                        id={id}
                        key={id}
                        letter={letter}
                        src={src}
                    />
                ))}</div>
            </AppWrapper>
            
        )
    }
}

export default App;