import React from 'react';
import styled from 'styled-components';

const DrumPadWrapper = styled.div `
    

border-radius: 5px;
box-shadow: 0 5px #fff;
    
    cursor: pointer;
    height: 100%;
    outline: inherit;
    width: 100%;


    

    .drum{
        align-items: center;
        background-color: #cdd7d6;
        border: none;
        border-radius: 5px;
        display: grid;
        justify-items: center;
        height: 100%;
        width: 100%;


        &:active{
            background-color: #b0bfbe;
            box-shadow: 0 3px #666;
            transform: translateY(4px);
        }

        &:hover{
            background-color: #b0bfbe;
            border-radius: 5px;
        }

        &:focus{
            background-color: #b0bfbe;
            border: none;
            outline: inherit;
            transform: translateY(4px);
        }

        &:focus::after{
            box-shadow: 0 3px #666;
            transform: translateY(4px) scale(0);
        }
    }
`

class DrumPad extends React.Component{
    componentDidMount() {
        document.addEventListener('keydown', this.handleKeydown)
        document.addEventListener('keyup', this.handleKeyUp)
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.handleKeydown)
    }

    handleKeydown = e => {
        if(e.keyCode === this.props.letter.charCodeAt()) {
            
            this.refs["drumButton" + this.props.letter].click()
            this.refs["drumButton" + this.props.letter].focus()
        }
    }

    handleKeyUp = e => {
        if(e.keyCode === this.props.letter.charCodeAt()) {
            this.refs["drumButton" + this.props.letter].blur()
        }
    }

    handleClick = () => {
        this.audio.play()
        this.audio.currentTime = 0
        this.props.handleDisplay(this.props.id)
        this.refs["drumButton" + this.props.letter].blur()
    }

    render() {
        return(
            <DrumPadWrapper>
                <button 
                    className='drum'
                    id={this.props.id}
                    onClick={this.handleClick}
                    ref = {"drumButton" + this.props.letter}    
                >
                    <h1>{this.props.letter}</h1>
                    <audio
                        className='audio-clip'
                        id = {this.props.letter}
                        ref = {ref => this.audio = ref}
                        src={this.props.src}
                    >
                    </audio>
                </button>
            </DrumPadWrapper>
        )
    }
}

export default DrumPad;
